@import url('https://fonts.googleapis.com/css?family=Aldrich');

.Soft {
    position: relative;
    width: 300px;
    height: 300px;
    margin-left: calc(20% - 240px);
    margin-top: 70px;
    background-color: #222222;
    display: inline-block;
    border-radius: 3px;
    text-align: center;
}

.Soft .img {
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    margin-left: 60px;
    margin-top: 40px;
    width: 180px;
    height: 180px;
    background-size: cover;
    cursor: pointer;
}

.Soft .img:hover {
    margin-left: 40px;
    margin-top: 20px;
    width: 220px;
    height: 220px;
}

.Soft .text {
    font-family: 'Aldrich', serif;
    font-size: 16px;
    color: white;
    margin-top: 20px;
    height: 20px;
}

.Soft .FrontLayer {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    cursor: pointer;
}


